import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { map, Observable, Subject } from 'rxjs';
import { ICreatePedidoPresupuesto, IItemPedido, IPedidoDePresupuesto } from '../models/pedido-de-presupuesto';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';

@Injectable()
export class PedidoDePresupuestoService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Rubros.
     * @returns Rubros
     */
    getPedidoDePresupuesto(sort?: any): Observable<Response[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.administracion.view_clientes}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token,
                    subject.next({
                        'list': resp.result,
                        // 'totalPages': resp.headers.get('x-pagination-page-count'),
                        // 'totalCount': resp.headers.get('x-pagination-total-count')
                    })
            });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param pedido_de_presupuesto 
     * @returns Crea un item nuevo
     */
    savePedidoDePresupuesto(pedido_de_presupuesto: ICreatePedidoPresupuesto): Observable<IPedidoDePresupuesto> {
        return this.http.post<IPedidoDePresupuesto>(`${environment.compras.compras_pedidos}`, pedido_de_presupuesto);
    }

    /**
     * Funcion en el servicio
     * @param idpedido
     * @param pedido_de_presupuesto
     * @returns Item actualizado actualizado
     */
    updatePedidoDePresupuesto(idpedido: number, pedido_de_presupuesto: ICreatePedidoPresupuesto): Observable<IPedidoDePresupuesto> {
        return this.http.put<IPedidoDePresupuesto>(`${environment.compras.compras_pedidos}` + '/' + idpedido, pedido_de_presupuesto);
    }

    /**
     * Funcion en el servicio
     * @param idpedido 
     * @returns Elimina un item
     */
    deletePedidoDePresupuesto(idpedido: number): Observable<IPedidoDePresupuesto> {
        return this.http.delete<IPedidoDePresupuesto>(`${environment.compras.compras_pedidos}` + '/' + idpedido);
    }

    getProveedores(): Observable<IProveedor[]> {
        let f = '?filter[activo]=1&sort=RazonSocial';
        return this.http.get<IProveedor[]>(`${environment.administracion.proveedores}${f}`);
    }

    cotizarPedidoDePresupuesto(pedido_de_presupuesto: IPedidoDePresupuesto): Observable<IPedidoDePresupuesto> {
        return this.http.post<IPedidoDePresupuesto>(``, pedido_de_presupuesto);
    }

    enviarPedidoDePresupuesto(pedido_de_presupuesto: IPedidoDePresupuesto): Observable<IPedidoDePresupuesto> {
        return this.http.post<IPedidoDePresupuesto>(``, pedido_de_presupuesto);
    }

    getArticulos(idarticulo?: number, filterLike?: string) {
        let f = `?filter[activo]=1&pagination=1&per-page=10&page=1`;
        if (idarticulo) {
            f += `&filter[idarticulo]=${idarticulo}`;
        } else if (filterLike) {
            f += `&filter[or][0][codigo_interno][like]=${filterLike}&filter[or][1][descripcion][like]=${filterLike}`;
        }

        let subject = new Subject<any>();
        this.http.get<any>(`${environment.stock.articulos}${f}`).pipe(
            map((resp => resp))
        ).subscribe((resp: any) => {
            this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
        });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getItemsPedido(idpedido: number): Observable<IItemPedido> {
        let f = `?filter[idpedido]=${idpedido}`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.compras.view_compras_pedido_item}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }
}