import { Component, Inject, OnInit } from '@angular/core';
import { IPedidoDePresupuesto } from '../../models/pedido-de-presupuesto';
import { PedidoDePresupuestoService } from '../../service/pedidos-de-presupuesto.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Subject, takeUntil } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-enviar-pedidos-de-presupuesto',
    templateUrl: './enviar.component.html',
    styleUrls: ['./enviar.component.scss']
})
export class EnviarComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    pedido_de_presupuesto: IPedidoDePresupuesto;
    title: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _PedidoDePresupuestoService: PedidoDePresupuestoService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<EnviarComponent>
    ) {
        this.pedido_de_presupuesto = this.data ? this.data : null;
        this.title = 'Enviar pedido de presupuesto';
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            fecha: new FormControl((this.pedido_de_presupuesto ? this.pedido_de_presupuesto.fecha : ''), Validators.required),
            observaciones: new FormControl((this.pedido_de_presupuesto ? this.pedido_de_presupuesto.observaciones : ''), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            let payload: IPedidoDePresupuesto = {
                fecha: this.form.get('fecha').value,
                observaciones: this.form.get('observaciones').value,
            }

            this._PedidoDePresupuestoService.cotizarPedidoDePresupuesto(payload)
                .subscribe({
                    next: (data) => {
                        if (data) {
                            this.onCloseMenu()
                        }
                    }
                });
        }
    }

    onCloseMenu() {
        this.dialogRef.close()
    }
}